import React from "react"
import { kebabCase } from "lodash"

import "./index.scss"

export const ContactCard = contact => (
  <div className="col-xs-12 col-sm-12 col-md-6 col-lg-4" key={contact.email}>
    <div className="box-row">
      <div className="card">
        <div className="card-content">
          <div className="card-header">
            <h3 className="card-title">
              {contact.address.city}, {contact.address.country}
            </h3>
          </div>
          <p className="card-description">
            <span>{contact.email}</span>
            <address>
              {contact.address.line01}
              <br />
              {contact.address.zipCode}, {contact.address.city}
            </address>
          </p>
        </div>
        <div className={`card-map map-${contact.address.city.toLowerCase()}`} />
      </div>
    </div>
  </div>
)

export const ServiceCard = ({ title, url, teaser }) => (
  <div className="col-xs-12 col-sm-12 col-md-6 col-lg-4" key={kebabCase(title)}>
    <div className="box-row">
      <div className="card">
        <div className="card-content">
          <div className="card-header">
            <h3 className="card-title">{title}</h3>
            {url && (
              <div className="card-arrow">
                <a href={url}>&#8674;</a>
              </div>
            )}
          </div>
          <p className="card-description">{teaser}</p>
        </div>
      </div>
    </div>
  </div>
)

export const TechnologyCard = ({ title, src }) => {
  const height = 137.15
  const width = height
  const margin = 50

  return (
    <i
      key={src}
      title={title}
      style={{
        height: `${height}px`,
        width: `${width}px`,
      }}
    >
      <div
        style={{
          opacity: 0.7,
          filter: "invert(1)",
          backgroundImage: `url(${src})`,
          backgroundPosition: "center center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "contain",
          height: `${height - margin}px`,
          margin: "0 auto",
          position: "relative",
          top: `${margin / 2}px`,
          width: `${width - margin}px`,
        }}
      />
    </i>
  )
}
